.publications-main{
    padding: 10vh 30px 0;    
    background-color: #2C3531;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 90vh;
}

.publications-container{
    padding: 30px;
    background: #4e555b;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    border: 4px solid transparent;
    border-radius: 7px;
    margin: 20px auto;
    width: 70%;
}

.publications-container h3{
    margin: 0;
}

.publications-container h3 a{
    transition: .3s all;
    color: #dbff09;
    text-decoration: none;
    font-size: 20px;
    font-weight: 200;
}

.publications-container div{
    color: rgb(255 255 255);
    line-height: 1.7;
    font-size: 16px;
    font-weight: 300;
}

.publications-main-title{
    margin-top: 2vh;
    font-weight: 200;
    font-size: 30px;
}