.homepage-landing{
  background-image: url(../img/aliasghar_1-removebg.png);
  background-repeat: no-repeat;
  background-size: 60% auto;
  background-position: right;
  height: 100vh;
  background-color: #2C3531;;
  display: grid;
  grid-template-columns: 50% 50%;
}

.homepage-overlay{
  background-color: rgba(0, 0, 0, 0.185);
  height: 100%;
}

.homepage-text-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 200;
    text-align: center;
    width: 73%;
    margin: auto;
}

.homepage-text{
  font-size: 70px;
}
.homepage-text-littel{
  letter-spacing: .2rem;
    color: rgba(255, 255, 255, 0.5) !important;
    text-transform: uppercase !important;
    line-height: 1.7;
    font-size: 16px;
    font-weight: 200;
}

.homepage-education{
  padding: 30px;
  background-color: white;
}

.homepage-education-container{
  display: grid;
  grid-template-columns: 20% auto;
  margin-bottom: 4vh;
}

.homepage-education-avatar{
  height: 14vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 14px;
}

.homepage-education-title{
  font-size: 30px;
  font-weight: bold;
  text-transform: uppercase;
}

.homepage-education-date{
  letter-spacing: .2rem;
  color: #263b62  !important;
  text-transform: uppercase !important;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 200;
}

.homepage-aboutme{
  background-color: #2C3531;
  padding: 30px 0;
  display: flex;
  flex-direction: column;
}

.homepage-aboutme-image{
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 80vh;
  margin: auto;
}

.homepage-aboutme-text{
  width: 83%;
  margin: auto;
  color: rgba(255, 255, 255, 0.7);
}

.homepage-aboutme-main-title{
  margin: auto;
  width: 83%;
  color: white;
  font-weight: 200;
  padding-top: 20px;
  font-size: 30px;
}

.homepage-getintouch{
  background-color: #2C3531;
  padding: 30px 0;
  text-align: center;
  font-weight: 200;
  padding-top: 20px;
  font-size: 30px;
  color: white;
}

.homepage-getintouch-icon-link{
  width: 60px;
  height: 60px;
  background: #5e5e5e;
  border-radius: 50%;
  font-size: 22px;
  display: flex;
  margin: 5px;
}

.homepage-getintouch-icon{
  color: white;
  margin: auto;
  height: 1.5em !important;
  width: 1.5em !important;
}

.homepage-getintouch-icons{
  display: flex;
  flex-direction: row;
  justify-content: center;
}