.navbar-list{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 10vh;
    width: 100%;
    color: white;
    z-index: 1;
}

.navbar-list-white{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: fixed;
    height: 10vh;
    width: 100%;
    background-color: white;
    color: black;
    z-index: 1;
}

.navbar-item{
    padding: 12px;
    font-size: 14px;
    font-weight: 400;    
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: .2em;
}

.navbar-item:hover{
    cursor: pointer;
}

.navbar-selected{
    color: #bac964 !important;
}