.works-main{
    padding-top: 10vh;
    background-color: #2C3531;
    color: white;
}

.works-submain{
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    /* column-count: 2;
    column-gap: 3%; */
}

.works-container{
    display: inline-block;
    border-radius: 5px;
    background-color: #4e555b;
    padding: 0 30px;
    width: 48%;
    width: 43%;
    padding: 0 22px;
    margin: 10px;
}

.works-description{
    line-height: 1.7;
    font-size: 15px;
    font-weight: 300;
    color: #cccccc;
}

.works-title{
    font-size: 20px;
    color: #fff;
    font-weight: 200;
}

.works-title a{
    color: #dbff09;
    text-decoration: none;
}

.works-title a:hover{
    color: #b2c251;
    text-decoration: none;
}

.works-main-title{
    margin-top: 2vh;
    font-weight: 200;
    font-size: 30px;
    margin-bottom: 20px;
}

.works-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 90vh;
}

.works-categories{
    display: flex;
}

.works-category{
    margin: 0 10px 10px 10px;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.4);
    line-height: 1.7;
    font-size: 16px;
    font-weight: 300;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
}

.works-category:hover{
    color: #bac964;
}

.works-category-selected{
    color: #bac964;
}

.works-description a{
    color: #dbff09
}