.honors-main{
    background-color: #2C3531;
    color: white;
    padding-top: 10vh;
    display: flex;    
    justify-content: center;
    flex-direction: column;
    align-items: center;
    min-height: 90vh;
}

.honors-container{
    padding: 17px 0 0 17px;
    background: #4e555b;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    border: 4px solid transparent;
    border-radius: 7px;
    margin: 20px 10px;
}

.honors-submain{
    width: 70%;
    display: grid;
    grid-template-columns: auto auto;
}

.honors-title{
    color: #dbff09;
    font-size: 20px;
    font-weight: 200;
}

.honors-description{
    color: rgba(255, 255, 255, 1);
    line-height: 1.7;
    font-size: 16px;
    font-weight: 300;
}

.honors-icon-calendar::before{    
    content: "\e979"; 
}

.honors-date{
    font-size: 12px;
    font-weight: 700;
    color: #fff;
}

.honors-main-title{
    margin-top: 2vh;
    font-weight: 200;
    font-size: 30px;
}

.honors-container-hidden{ 
    display: none;
}

.honors-container-fade-out{
    transform: scale(0);    
}